.header {
  height: 50px;
  position: sticky;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #2ba4a0;
  padding: 0 20px;
  .header_menu {
    height: 100%;
    list-style: none;
    margin: 0;
    display: flex;
    align-items: center;
    .header_menu_item_link {
      text-decoration: none;
      color: #fff;
    }
  }
}
